.task-list {
    list-style-type: none;
  }

.task-list-container {
    margin-bottom: 1rem;
  }
  
  .task-list-input {
    margin-bottom: 1rem;
    padding: 0.5rem;
  }
  
  .task-list-button {
    margin-right: 0.5rem;
  }
  
  .task-list-delete-button {
    background-color: #3652f4;
    color: white;
    border: none;
    padding: 0.25rem 0.5rem;
    cursor: pointer;
    margin-left: 0.5rem;
  }